    width: 100%;
    height: 100vh;
    z-index: 99;
    background-color: var(--header-background);
    transition: transform .5s linear;
    position: absolute;
    left: 0;
    overflow: auto;
    transform: translateX(-100%);
  }

  .burger__menu.is-open {
    transform: translateX(0);
  }
}

@media screen and (max-width: 1199px) {
  .burger__menu {
    width: 100%;
    height: 100vh;
    z-index: 99;
    background-color: var(--header-background);
    transition: transform .5s linear;
    position: absolute;
    top: 65px;
    left: 0;
    transform: translateX(-100%);
