.product-item-title {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--title-black-color);

  padding-top: 120px;
  padding-bottom: 40px;

  @media (min-width: $tablet) {
    font-size: 22px;
    line-height: 1.22;
  }

  @media (min-width: $desktop) {
    padding-top: 165px;
    padding-bottom: 65px;
    font-size: 30px;
    line-height: 1.3;
  }
}

.product-item-subtitle {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 176%;
  text-align: center;
  letter-spacing: 0.04em;

  padding-left: 40px;
  padding-right: 40px;

  @media (min-width: $tablet) {
  }

  @media (min-width: $desktop) {
    font-size: 16px;
  }
}

.product__picture {
  position: absolute;
  display: block;
  width: 200px;
  height: 200px;
  top: -100px;
  left: 0;
  right: 0;
  margin: auto;

  @media (min-width: $tablet) {
    top: -120px;
  }

  @media (min-width: $desktop) {
    width: 302px;
    height: 302px;
    top: -180px;
  }
}

.product-arrow {
  display: block;
  width: 40px;
  height: 16px;
  fill: var(--title-black-color);
  // margin-bottom: 16px;
  position: absolute;
  bottom: 16px;
  cursor: pointer;

  @media (min-width: $desktop) {
    bottom: 60px;
  }
}

.product-text-wraper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
